body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

img.avatar {
  width: 175px;
  height: 175px;
  border-radius: 128px;
  box-shadow: 10px 10px 20px #e5e5e5;
  margin-bottom: 15px;
}


.headerlogo{
  display: inline-block;
  float: left;
  margin-left: 10px;
  width: 150px;
}

.avatar-header{
  display: inline-block;
  width: 30px;
  height: 30x;
  border-radius: 128px;
}
.header-elements{
  display: inline-block;
  float: right;
  margin-right: 20px;

}
.header-elements h4{
  display: inline-block;
  color:white;
  margin-bottom: 0px !important;
  margin-right: 15px;
}

.email-form{
  margin: 0 auto;
  width: 315px;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #17242a;
  height: 50px;
  padding:10px;
  margin-bottom: 40px;
  color: white;
}

.App-title {
  font-size: 1.5em;
  color: white;
}

.App-intro {
  font-size: large;
}

button {
  font-size: 1.2em;
  cursor: pointer;
  box-shadow: 10px 10px 20px #e5e5e5;
  color: white;
}

.blockstack-logo{
  width: 20px;
}

.signin-btn{
  height: 35px !important;
  background-color: white !important;
  box-shadow: 0 0 11px rgba(0, 0, 0, 0.2) !important;
  border: none;
  color: #17242a !important;
  
}
.signout-btn{
  height: 35px !important;
  background-color: white !important;
  box-shadow: 0 0 11px rgba(0, 0, 0, 0.2) !important;
  border: none;
  color: #17242a !important;
}
.link-signout{
  text-decoration: underline;
  color: white;
  margin-left: 15px;
  padding: 2px;
}

.signin-btn:hover{
  color: white !important;
  background-color: #5cdb95 !important;
  border:none;
}

.signin-btn-text{
  padding: 2px;
  margin-left: 10px;
}

.logo{
  margin-bottom: 35px;
}

.signin-container{
  height: 50%;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
}

.separator{
    margin: 0 auto 14px;
    border-top: #3d9068 2px solid;
    width: 52px;
}
.text-center {
    text-align: center;
  }
  .header-logo-center{
    text-align: center;
    width: 20vw;
  }
  .header-logo-left{
    text-align: left;
    width: 20vw;
  }
  .header-logo-right{
    text-align: right;
    width: 20vw;
  }
  .participant-placeholder{
    border-bottom: 2px solid black;
    /* width: 20vw;  */
    margin: 0 auto;
  }
  .text-uppercase{
    text-transform: uppercase;
  }
  
  .cert-border{
    border: #5c5c5c 2px solid;
    padding: 20px;
    margin: 20px;
  }
  
  .partner-logo{
    background-origin: content-box;
    background-position: center center;
    background-size: contain;
    background-repeat: no-repeat;
    height: 120px;
  }
  .qr-container{
    /* background-origin: content-box; */
    background-position: center center;
    background-size: contain;
    background-repeat: no-repeat;
    height: 190px;
    width: 190px !important;
    border: black solid 1px;
    background-color: white;
  }
