.email-form{
  margin: 0 auto;
  width: 315px;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #17242a;
  height: 50px;
  padding:10px;
  margin-bottom: 40px;
  color: white;
}

.App-title {
  font-size: 1.5em;
  color: white;
}

.App-intro {
  font-size: large;
}

button {
  font-size: 1.2em;
  cursor: pointer;
  box-shadow: 10px 10px 20px #e5e5e5;
  color: white;
}

.blockstack-logo{
  width: 20px;
}

.signin-btn{
  height: 35px !important;
  background-color: white !important;
  box-shadow: 0 0 11px rgba(0, 0, 0, 0.2) !important;
  border: none;
  color: #17242a !important;
  
}
.signout-btn{
  height: 35px !important;
  background-color: white !important;
  box-shadow: 0 0 11px rgba(0, 0, 0, 0.2) !important;
  border: none;
  color: #17242a !important;
}
.link-signout{
  text-decoration: underline;
  color: white;
  margin-left: 15px;
  padding: 2px;
}

.signin-btn:hover{
  color: white !important;
  background-color: #5cdb95 !important;
  border:none;
}

.signin-btn-text{
  padding: 2px;
  margin-left: 10px;
}

.logo{
  margin-bottom: 35px;
}

.signin-container{
  height: 50%;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
}

.separator{
    margin: 0 auto 14px;
    border-top: #3d9068 2px solid;
    width: 52px;
}